<style lang="css" scoped>
h2{
    margin: 20px 0;
    font-size: 30px;
    font-weight: 800;
}
.date{
    margin: 15px 0;
}
p{
    color:#a3a3a3;
}
</style>
<template lang="html">
    <div class="summary">
        <h2>{{data.title}}</h2>
        <p>
            {{data.summary}}
        </p>
        <div class="date">
            <em>Post by <strong>WangYe</strong> on <strong>{{data.date}}</strong></em>
        </div>
    </div>
</template>

<script>
export default {
    props: ['data']
}
</script>



