<style scoped>
.home {
    margin: 0;
    width: 100%;
    overflow: hidden;
}

.postlist-container>div {
    border-bottom: 1px solid #ddd;
    cursor: pointer;
}

.postlist-container>div:last-child {
    border-bottom: none;
}

</style>
<template>
    <div class="row">
        <div class="col-lg-8 col-lg-offset-1 col-md-8 col-md-offset-1 col-sm-12 col-xs-12 postlist-container">
            <div v-for="(list, index) in lists" v-if='index < pageNum * page && index > pageNum * ( page - 1)'>
                <router-link :to="{path: list.path}" tag="div">
                    <summary-list :data="list"></summary-list>
                </router-link>
            </div>
            <nav aria-label="...">
                <ul class="pager">
                    <li class="previous":class="{disabled: page === 1}"><a href="javascript:void(0)" @click='older'><span aria-hidden="true">&larr;</span> Older</a></li>
                    <li class="next" :class="{disabled: page === totalPage}"><a href="javascript:void(0)" @click='newer'>Newer <span aria-hidden="true">&rarr;</span></a></li>
                </ul>
            </nav>
        </div>
        <div class="col-lg-3 col-lg-offset-0 col-md-3 col-md-offset-0 sidebar-container visible-lg-block visible-md-block">
            <tags></tags>
            <about></about>
        </div>
    </div>
</template>
<script>
import summaryList from '../components/summary.vue'
import tags from '../components/tags.vue'
import about from '../components/about.vue'
import {
    mapGetters
} from 'vuex'
export default {
    data() {
        return {
            totalPage: 0,
            pageNum: 9,
            page: 1
        }
    },
    components: {
        summaryList,
        tags,
        about
    },
    computed: {
        // 使用对象展开运算符将 getters 混入 computed 对象中
        // ...mapGetters([
        //     'getArticles'
        // ])
        lists() {
            let articles = this.$store.getters.getArticles
            this.totalPage = Math.ceil(articles.length / this.pageNum)
            return this.quickSort(articles)
        }
    },
    methods: {
        // 降序
        quickSort(arr) {
            if (arr.length <= 1) {
                return arr
            }
            let left = []
            let right = []
            let flag_index = Math.floor(arr.lenght / 2)
            let flag = arr.splice(flag_index, 1)[0]
            for (let i = 0, len = arr.length; i < len; i++) {
                if (new Date(arr[i].date).getTime() > new Date(flag.date).getTime()) {
                    left.push(arr[i])
                } else {
                    right.push(arr[i])
                }
            }
            return this.quickSort(left).concat([flag], this.quickSort(right))
        },
        newer() {
            console.log(this.page)
            console.log(this.page<this.totalPage)
            return this.page < this.totalPage ? this.page ++ : this.page
            // this.page ++
        },
        older() {
            return this.page > 1 ? this.page -- : this.page
            // this.page >= 1
            // this.page --
        }
    }
}

</script>



