<style scoped>
.about{
    padding: 5px;
    margin-bottom: 15px;
    border-top: 1px solid #ddd;
    cursor: pointer;
}
h2 {
    margin: 10px 0;
    color:#666;
}
img{
    width: 180px;
    height: 180px;
    border: 1px solid #ddd;
    padding: 2px;
}
blockquote{
    margin-top: 10px;
    border-left: 4px solid #eee;
    padding: 15px;
    padding-right: 0;
}
p{
    color:#bbb;
    margin-bottom: 20px;
}
span{
    position: relative;
    padding-left: 20px;
    font-size: 16px;
    color:#999;
    font-style: italic;
}
span::before{
    content:'\2014 \00A0';
}
</style>
<template lang="html">
    <router-link :to="{path: '/aboutme'}" tag="div" class="about">
        <h2>ABOUT ME</h2>
        <img :src="kobe" alt="">
        <blockquote>
            <p>知天易，易艰难</p>
            <span>wangye</span>
        </blockquote>
    </router-link>
</template>

<script>
import kobe from '../assets/images/kobe.jpg'
export default {
    data() {
        return {
            kobe
        }
    }
}
</script>



