<style scoped>
h2{
    color:#666;
    margin: 10px 0;
    cursor: pointer;
}
.tags{
    padding: 5px;
    margin-bottom: 15px;
    border-top: 1px solid #ddd;
}
span{
    background-color: #777;
    border-radius: 3px;
    color:#FFF;
    padding: 2px 5px;
    font-size: 14px;
    cursor: pointer;
    margin-right: 5px;
    margin-bottom: 5px;
}
.tags div{
    display: -webkit-flex;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}
</style>
<template lang="html">
    <div class="tags">
        <router-link :to="{path: '/tag'}" tag="h2">TAGS</router-link>
        <div>
            <router-link :to="{path: '/tag', query: {tagName: tag}}" tag='span' v-for='tag in tags' :key="tag">{{tag}}</router-link>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tags: this.$store.state.tags
        }
    }
}
</script>



